import React from 'react';
import Style from './About.module.scss';
import Terminal from "./Terminal";
import {Box} from "@mui/material";
import {info} from "../../info/Info";


export default function About() {
    const firstName = info.firstName.toLowerCase()

    function aboutMeText() {
        return <>
            <p>
            Hello! Thanks for checking out my page. I study Computer Science at UCLA, with a particular interest in deep learning, computer vision, and biotech. 
            </p>
            <p>.</p>
            <p>
            I spent last summer interning with SpaceX, where I wrote software and designed systems to improve the Droneships and Octagrabber robots.
            </p>
            <p>.</p>
            <p>
            Currently, alongside my coursework, I do deep learning research on MRI images with UCLA Health's Brain Tumor Imaging Laboratory, which is under the Center for Computer Vision and Imaging Biomarkers
            </p>
            <p>.</p>
            <p>
            I am looking for Summer 2024 internships in software engineering and data science!
            </p>
            
        </>;
    }

    function skillsText() {
        return <>
            <p><span style={{color: info.baseColor}}>{firstName}{info.lastName.toLowerCase()} $</span> cat skills/tools
            </p>
            <p style={{color: info.baseColor}}> Proficient With</p>
            <ul className={Style.skills}>
                {info.skills.proficientWith.map((proficiency, index) => <li key={index}>{proficiency}</li>)}
            </ul>
            <p style={{color: info.baseColor}}> Familiar With</p>
            <ul className={Style.skills}>
                {info.skills.exposedTo.map((skill, index) => <li key={index}>{skill}</li>)}
            </ul>
        </>;
    }

    function miscText() {
        return <>
            <p><span style={{color: info.baseColor}}>{firstName}{info.lastName.toLowerCase()} $</span> cat
                hobbies/interests</p>
            <ul>
                {info.hobbies.map((hobby, index) => (
                    <li key={index}><Box component={'span'} mr={'1rem'}>{hobby.emoji}</Box>{hobby.label}</li>
                ))}
            </ul>
        </>;
    }

    return (
        <Box display={'flex'} flexDirection={'column'} alignItems={'center'} mt={'3rem'}>
            <Terminal text={aboutMeText()}/>
            <Terminal text={skillsText()}/>
            <Terminal text={miscText()}/>
        </Box>
    )
}